import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useBEM from '../../utils/hooks/useBEM';
import LensConsultationBrand from './components/LensConsultationBrand';
import LensConsultationProduct from './components/LensConsultationProduct';
import LensConsultationSide from './components/LensConsultationSide';
import LensConsultationType from './components/LensConsultationType';
import lensConsultationSlice, { getLensConsultation } from './../../store/slices/lens-consultation.slice';
import SlideView from '../../components/SlideView';
import Button from '../../components/ui/Button';

interface Props {

}

const LensConsultation : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-consultation');
    const dispatch = useDispatch();
    const { sliderPage, lensType, lensBrand} = useSelector(getLensConsultation); 

    // HOOKS
    useEffect(() => {
        return () => {
            dispatch(lensConsultationSlice.actions.setSliderPage(0));
            dispatch(lensConsultationSlice.actions.selectLensType(null));
        }
    },[]);

    // METHODS
    const slideBack = () => {
        dispatch(lensConsultationSlice.actions.setSliderPage(sliderPage - 1));

        if(sliderPage === 1){
            setTimeout(() => {
                dispatch(lensConsultationSlice.actions.selectLensType(null));
            }, 250);
        }
    }

    const getBackButtonLabel = () => {
        let label: string | undefined = '';
        if(sliderPage < 2) label = lensType?.label;
        if(sliderPage === 2) label = lensBrand?.label;
        return label;
    }

    return (
        <div className={B() + ' page'}>
            <LensConsultationSide/>

            <div className={E('slide-container')}>

                <Button 
                    click={slideBack}
                    outlineType="light"
                    className={E('slide-back-button', [sliderPage > 0 ? 'activate' : ''])}
                    text={getBackButtonLabel()} 
                    icon="left-arrow-circle-gray" type="secondary"                  
                    iconPosition="left"/>


                <SlideView id="lensConsultation" numberOfSlides={3} currentSlide={sliderPage}>                
                    <LensConsultationType/>
                    <LensConsultationBrand/>
                    <LensConsultationProduct/>                           
                </SlideView>
            </div>
        </div>
    )
}

export default LensConsultation;