import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from '../../components/Modal';
import Button from '../../components/ui/Button';
import InputText from '../../components/ui/InputText';
import routes from '../../constants/routes.constant';
import userSlice, { getUser } from '../../store/slices/user.slice';
import withAnimationTransition from '../../utils/hoc/withAnimationTransition';
import useBEM from '../../utils/hooks/useBEM';
import LensProposaBuilderlExperience from './components/LensProposaBuilderlExperience';
import LensProposalBuilderCard from './components/LensProposalBuilderCard';
import LensProposalBuilderFooter from './components/LensProposalBuilderFooter';

interface Props {

}

const LensProposalBuilder : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM(routes.lensProposalBuilder.id);
    const [isExperience, setIsExperience] = useState(false);
    const [modifyId, setModifyId] = useState(false);

    const [modifyIDField, setModifyIDField] = useState<string>('');
    const dispatch = useDispatch();
    const { id } = useSelector(getUser);



    // HOOKS
    useEffect(() => {
        setModifyIDField(id || '');
    }, [id])

    // METHODS
    const footerActionListener = (action: string) => {
        if(action === 'id') 
            setModifyId(true);
        else 
            setIsExperience(!isExperience);
    }

    const lensConsultationFooter = () => {
        return <label className="impacted">
            Impacted by: <span>Need Explorer</span> <span>Prescription</span>
        </label> 
    }

    const modalOnClose = () => {
        setModifyId(false);
    }

    const idOnchange = (event: any) => {
        setModifyIDField(event.target.value);
    }

    const editId = () => {
        // TODO VALIDATE ID HERE
        if(modifyIDField.length < 6)return;
        dispatch(userSlice.actions.setId(modifyIDField));
        setModifyId(false);        
    }

    return (
        <div className={B()}>
            
            <div className={E('main', [isExperience ? 'experienced' : ''])}>     

                <LensProposalBuilderCard route={routes.needExplorer} label="Need Explorer" icon="pencil" completed={true}/>
                <LensProposalBuilderCard route={routes.prescription} label="Prescription" icon="pencil" completed={true}/>
                <LensProposalBuilderCard route={routes.measures} label="Measures" icon="plus"/>
                <LensProposalBuilderCard route={routes.lensConsultation} label="Lens Consultation" icon="plus" footer={lensConsultationFooter()}/>
                <LensProposalBuilderCard route={routes.delivery} label="Delivery" icon="plus" className="delivery"/>

                <LensProposalBuilderFooter isExperience={isExperience} onActionTrigger={footerActionListener}/>
                <div className={E('overlay')}/>
            </div> 

            <LensProposaBuilderlExperience/>


            <Modal show={modifyId} onClose={() => modalOnClose()}>
                <div className="home__start-from-id-popup">
                    <h2>Modify ID</h2>
                    
                    <div>
                        <InputText
                            value={modifyIDField}
                            placeholder="Enter an ID" 
                            maxlength={6} 
                            onChange={idOnchange}/>
                        
                        <Button text="Next" icon="right-arrow-circle-white" click={editId}/>                         
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default withAnimationTransition(LensProposalBuilder, {
    entrance: 'FADE_IN'
});
